* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: #808080;
  font-family: "myriad-pro", Helvetica, Arial, sans-serif;
}
.Nav_top {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.C_logo {
  padding-top: 40px;
}
.Need_hlp {
  display: flex;
  justify-content: flex-end;
}
.Need_hlp a {
  text-decoration: none;
  color: #008bdf;
}
.Need_hlp p {
  width: 280px;
  background-color: rgba(128, 128, 128, 0.527);
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0 0 10px 10px;
}
.Menu {
  display: none;
}
.Company_list {
  width: 100%;
  height: 100px;
  background-color: #60b9ee;
  display: flex;
  padding: 0 4rem;
  align-items: center;
  justify-content: space-between;
}
.Company_location {
  color: #fff;
}
.About_Apply {
  background-color: #ebebeb;
  display: flex;
  justify-content: center;
  padding: 1.3rem 0;
  width: 100%;
}
.About_dscrptn_box {
  background-color: #fff;
  width: 23%;
  height: 550px;
  border: 1px solid #e0e0e0;
  border-radius: 5px;
  margin: 15px;
}
.ABout_para_btn h3 {
  padding: 20px;
}
.ABout_para_btn p {
  margin: 0 0 10px 0;
  padding: 0 20px 20px 20px;
}
.Apply_box {
  background-color: #fff;
  width: 67%;
  height: 550px;
  border-radius: 5px;
  border: 1px solid #e0e0e0;

  margin: 15px 30px 15px 15px;
}
.ABout_para_btn {
  margin-bottom: 15px;
}
.side_para {
  border-top: 1px solid #e0e0e0;
  font-size: 16px;
}
.side_VJDbtn,
.Start_Apply_btn {
  text-align: center;
}
.side_VJDbtn button {
  border-radius: 20px;
  border: 3px solid #80c844;
  background-color: #fff;
  color: #80c844;
  font-weight: 600;
  padding: 7px 10px 7px;
  cursor: pointer;
}
.side_VJDbtn button:hover {
  background-color: #80c844;
  color: #fff;
}
.side_para p {
  margin: 20px 0 20px 0;
  font-size: 16px;

  padding: 0 20px 20px 20px;
}
.Apply_social {
  display: flex;
  border-bottom: 1px solid #e0e0e0;
}
.Company_list .list ol {
  display: flex;
}
.Company_list .list ol li {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
  color: #fff;
}
.list .fa-long-arrow-alt-right {
  padding: 4px 4px 0 4px;
  font-size: 25px;
  cursor: default;
}
.Socila_share_icons {
  display: flex;
  align-items: center;
}
.Socila_share_icons #Icons {
  display: flex;
}
.Socila_share_icons a {
  display: flex;
  border-radius: 20px;
  width: 70px;
  margin: 0 5px;
  align-items: center;
  justify-content: space-around;
  flex-direction: row;
}
#Icons .twitter_btn {
  border: none;
  background-color: #1da1f2;
  color: white;
  font-weight: 600;
  padding: 6px 10px;
}
#Icons .facebook_btn {
  border: none;
  background-color: #3c579e;
  color: white;
  font-weight: 600;
  padding: 6px 10px;
}
#Icons .linkedin_btn {
  border: none;
  background-color: #0178b5;
  color: white;
  font-weight: 600;
  padding: 6px 10px;
}
.social_share_img {
  height: 24px;
}
.Apply_box .Apply_now {
  padding: 15px 30px;
}
.Apply_box .Apply_now h1 {
  color: #0093dd;
  font-size: 48px;
}
.report_qstns_p p {
  padding: 15px 30px;
  font-size: 19px;
}
.report_tools_p {
  padding: 10px 0 30px 60px;
}
.report_tools_p p {
  padding: 4px;
}
.report_tools_p .rghtarrow_btn {
  color: #0093dd;
  font-size: 23px;
  padding: 0 7px 0 0;
}
.applicaion_box {
  width: 100%;
  height: 250px;
  display: flex;
  background-color: #c1e1f1a6;
}
.applicaion_box .email_input {
  padding: 40px 0 0 5px;
  width: 50%;
  display: flex;
  border-right: 1px solid grey;
  height: 100%;
  flex-direction: column;
}
.applicaion_box .email_input label {
  display: block;
  font-size: 14px;
  text-align: center;
  font-weight: bold;
}
#icon_field {
  padding: 4px 0 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
#icon_field span {
  border: 1px solid #979696;
  border-radius: 4px 0 0 4px;
  border-right: 0;
  color: #0093dd;
  background-color: #fff;
  padding: 6.9px;
}
#icon_field input {
  border: 1px solid #979696;
  padding: 6px;
  width: 300px;
  outline: none;
  font-size: 17px;
  border-radius: 0 4px 4px 0;
}
.go_btn {
  margin: 15px 0;
  text-align: center;
}
.go_btn button {
  border: none;
  width: 60px;
  background-color: #80c844;
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  padding: 7px 10px 7px;
  cursor: pointer;
  border-radius: 20px;
}
.go_btn button:hover {
  background-color: #fff;
  color: #80c844;
  border: 2px solid #80c844;
}
.Social_login {
  display: flex;
  margin: auto;

  flex-direction: column;
}
.Social_login img {
  background-color: #fff;
}

.Social_ntwrk p {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
  padding-bottom: 5px;
  border-bottom: 2px solid #808080;
}
.Social_btns p {
  padding-left: 5px;
}
.Social_login .Google,
.Social_login .Facebook,
.Social_login .Linkedin {
  display: flex;
  align-items: center;
  padding: 5px;
  cursor: pointer;
  width: 280px;
  font-size: 17px;
  margin-bottom: 10px;
  border: 1px solid #979696;
  background-color: #008adfc4;
  color: #fff;
  border-radius: 5px;
}

